body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: inter;
}

nav {
  color: #fff;
  background-color: #1c212e;
  border-radius: 50px;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  margin: 10px;
  padding: 10px 30px;
  display: flex;
}

nav ul {
  gap: 20px;
  text-decoration: none;
  list-style: none;
  display: flex;
}

nav ul li a {
  color: #fff;
  font-size: 16px;
  font-weight: 200;
  text-decoration: none;
}

.HeroSection {
  background: linear-gradient(#1c212e, #223055);
  border-radius: 20px;
  justify-content: center;
  align-items: center;
  height: 70vh;
  margin: 10px;
  display: flex;
}

.HeroSection__text {
  text-align: center;
  color: #fff;
  width: 50%;
}

.HeroSection__text h1 {
  font-size: 50px;
  font-weight: 700;
  line-height: 50px;
}

.HeroSection__text p {
  font-size: 20px;
  font-weight: 200;
  line-height: 30px;
}

.CTAHero {
  color: #fff;
  background: linear-gradient(to bottom left, #5673bd, #223055);
  border: 2px solid #f8f8f866;
  border-radius: 50px;
  width: 130px;
  height: 50px;
  box-shadow: 0 4px 20px #0a0f1a4d;
}

.CTAHero:hover {
  cursor: pointer;
  color: #fff;
  background: linear-gradient(#1c212e, #223055);
  border: 1px solid #fff;
  transition: all .3s ease-in-out;
  transform: translateY(-2px);
  box-shadow: 0 4px 20px #0a0f1a99;
}

.formSection {
  background: linear-gradient(#1c212e, #223055);
  justify-content: center;
  align-items: center;
  height: 80vh;
  display: flex;
}

.formSection__content {
  background-color: #fff;
  border: 3px solid #0e53814d;
  border-radius: 5px;
  width: 70%;
  height: 80%;
  padding: 5px;
  display: flex;
  box-shadow: 0 10px 30px #2d52f833;
}

.formImgContainer {
  background-image: url("booooks.2406f2d2.jpg");
  background-size: cover;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 100%;
  display: flex;
}

form {
  background-color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
  height: 100%;
  display: flex;
}

form input {
  background-color: #d1e0f766;
  border: none;
  border-radius: 30px;
  width: 50%;
  margin-bottom: 5px;
  padding: 15px;
}

form button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(to bottom left, #5673bd, #223055);
  border: 2px solid #f8f8f866;
  border-radius: 50px;
  width: 54%;
  padding: 15px;
  box-shadow: 0 4px 20px #0a0f1a4d;
}

form input:focus {
  background-color: #d1e0f733;
  border: 2px solid #f8f8f866;
  outline: none;
}

.bookList {
  padding: 20px;
}

.bookList h1 {
  text-align: center;
  color: #000;
  font-size: 40px;
  font-weight: 700;
}

.sortContainer {
  background: linear-gradient(to bottom left, #cad9ff, #cdd4e7);
  border: 2px solid #f8f8f866;
  border-radius: 50px;
  justify-content: space-between;
  align-items: center;
  gap: 20px;
  width: 30%;
  margin: auto;
  padding: 0 10px 0 20px;
  display: flex;
  box-shadow: 0 4px 20px #3b52834d;
}

.sortContainer h2 {
  font-size: 17px;
  font-weight: 400;
}

.sortContainer button {
  border: none;
  border-radius: 30px;
  width: 80px;
  height: 30px;
}

.sortContainer button:focus {
  color: #fff;
  background: linear-gradient(to bottom left, #5673bd, #223055);
  border: 2px solid #f8f8f866;
  box-shadow: 0 4px 20px #0a0f1a4d;
}

.bookCategoryCard {
  background-color: #f4f8ff;
  border-radius: 30px;
  flex-direction: column;
  margin: 10px;
  padding: 0 0 20px 20px;
  display: flex;
}

.bookCategoryCard .categoryHeader {
  text-transform: capitalize;
  background-color: #0000;
  border: 1px solid #000;
  border-radius: 20px;
  width: max-content;
  padding: 10px 20px;
  font-size: 15px;
  font-weight: 200;
}

.categoryLine {
  background-color: #1a1a1a80;
  border: none;
  width: 100%;
  height: 1px;
}

.bookCategorySections {
  justify-content: start;
  align-items: center;
  gap: 20px;
  display: flex;
}

button a {
  color: #fff;
  text-decoration: none;
}

.bookCard {
  border-radius: 20px;
  width: 180px;
  padding: 10px;
}

.bookCard button {
  height: 30px;
}

.bookCardButtons {
  gap: 10px;
  display: flex;
}

.deleteBook {
  cursor: pointer;
  background-color: #0000;
  border: 1px solid #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  display: flex;
}

.editBook {
  color: #fff;
  background: linear-gradient(to bottom left, #5673bd, #223055);
  border: none;
  border-radius: 20px;
  width: 80%;
  padding: 5px 10px;
}

.editBook:hover {
  cursor: pointer;
  color: #fff;
  background: linear-gradient(#323d57, #41558b);
  box-shadow: 0 4px 20px #374e8199;
}

.book-title {
  text-align: left;
  text-transform: capitalize;
  background: linear-gradient(to bottom left, #cad9ff, #899bcc);
  width: 100%;
  height: 120px;
  padding: 5px;
}

#editBook {
  z-index: 2;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 0;
  width: 100%;
  height: 100%;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

#editBook h2 {
  color: #fff;
  text-align: center;
  font-size: 30px;
  font-weight: 700;
}

#editBook:before {
  content: "";
  z-index: -1;
  backdrop-filter: blur(10px);
  background: linear-gradient(#1c212e80, #223055);
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  overflow: hidden;
}

#editBookForm {
  height: 60%;
  margin: auto;
  overflow: hidden;
}

.cancelEdit {
  cursor: pointer;
  background-color: #f705051a;
  border: 2px solid #803c3c;
  border-radius: 50%;
  margin-bottom: 20px;
  padding: 10px;
}

#feedbackContainer {
  pointer-events: none;
  background-color: #0000;
  flex-direction: column;
  align-items: start;
  width: 25%;
  height: 95vh;
  display: flex;
  position: fixed;
  top: 0;
  left: 20px;
}

.feedbackCard {
  background-color: #fff;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 50px;
  margin: 10px;
  display: flex;
  box-shadow: 0 4px 20px #0a0f1a4d;
}

#chatbot-container {
  backdrop-filter: blur(10px);
  background: linear-gradient(#1c212e80, #223055);
  border: 2px solid #ffffff4d;
  border-radius: 10px;
  width: 300px;
  padding: 10px;
  position: fixed;
  bottom: 20px;
  right: 20px;
  box-shadow: 0 4px 20px #0a0f1a4d;
}

#chat-history {
  border-bottom: 1px solid #ccc;
  height: 220px;
  margin-bottom: 10px;
  padding: 5px;
  overflow-y: auto;
}

#chat-input {
  color: #fff;
  background-color: #d1e0f74d;
  border: 2px solid #fff3;
  border-radius: 5px 0 0 5px;
  width: calc(100% - 60px);
  padding: 5px 5px 5px 10px;
}

#chat-input::placeholder {
  color: #ccc;
}

#chat-input:focus {
  background-color: #d1e0f733;
  outline: none;
}

.chatbot-header {
  color: #fff;
  justify-content: center;
  margin-top: 10px;
  display: flex;
}

#send-btn {
  cursor: pointer;
  color: #fff;
  background: linear-gradient(#1c212e, #020307);
  border: none;
  border-radius: 0 5px 5px 0;
  height: 34px;
  padding: 5px 10px;
}

.user-history {
  background-color: #e0f7fa;
  border-radius: 5px;
  width: 70%;
  margin: 5px 0 5px auto;
  padding: 10px;
  display: flex;
}

.ai-history {
  background-color: #ffe0b2;
  border-radius: 5px;
  width: 70%;
  margin: 5px 0;
  padding: 10px;
  display: flex;
}

.chat-input-container {
  display: flex;
}

@media (width <= 768px) {
  .HeroSection__text {
    width: 80%;
    height: max-content;
    font-size: 15px;
  }

  .formImgContainer {
    display: none;
  }

  .formSection__content, form {
    width: 100%;
  }

  .sortContainer {
    width: 80%;
  }
}
